body {
  margin: 0;
}

.page {
  font-family: "Poppins", Arial, sans-serif;
  color: $white-color;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;

  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    opacity: 0;
    transition: all 0.3s;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }

  &:has(.page__menu:target) {
    overflow: hidden; /* not to scroll the page */
  }
}
