.menu {
  box-sizing: border-box;
  height: 100vh;

  font-weight: 600;
  text-transform: uppercase;
  color: $main-text-color;

  background-color: #c2c2c2;

  overflow: auto;

  &__top {
    position: sticky;
    top: 0;
    z-index: 1;

    margin-bottom: 24px;

    @include onTablet {
      margin-bottom: 0;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;

    font-size: 16px;
    line-height: 27px;
    color: inherit;
    text-decoration: none;

    @include hover(color, $hover-red-color);
  }

  &__book-a-test-ride {
    display: block;
    width: fit-content;

    padding-bottom: 7px;
    border-bottom: 1px solid $main-text-color;

    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: inherit;
    text-decoration: none;
  }
}
